.btn-nueva-tarea {
  color: #FFFFFF;
  background: #9f81f7;
  font-size: 40rem;
  border-radius: 50%;
  padding: 1.8rem 2rem;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 3rem;
  margin-bottom: 4rem;
  transform: scale(1.1);
  cursor: pointer; }
  @media (min-width: 768px) {
    .btn-nueva-tarea {
      transform: scale(1.4); } }

.header-principal {
  background: #9f81f7; }
  .header-principal .barra {
    position: sticky;
    top: 0;
    background: #9f81f7; }
    .header-principal .barra .contenido-barra {
      display: flex;
      align-items: center;
      justify-content: space-between; }
      .header-principal .barra .contenido-barra i {
        font-size: 4.5rem;
        color: #FFFFFF;
        cursor: pointer; }
    .header-principal .barra .menu a {
      display: block;
      color: #FFFFFF;
      margin: .5rem 0; }
      .header-principal .barra .menu a:last-child {
        margin: 0; }

.brand p {
  font-size: 3.5rem;
  font-weight: 700;
  margin: 0;
  color: #FFFFFF; }

.brand h1 {
  padding: 2rem 0;
  font-weight: 300;
  color: #FFFFFF;
  margin: 0;
  font-size: 2rem;
  background: #d358f7;
  text-align: center; }

@media (min-width: 768px) {
  .contenedor-tareas {
    display: grid;
    gap: 0.5rem;
    grid-template-columns: repeat(3, 1fr); } }

.tarea {
  background: #e1e1e1;
  padding: 1rem 1rem;
  border-radius: 1.5rem;
  transition: all .3s ease-in-out; }
  .tarea:hover {
    background: #d7d7d7; }
  .tarea .tarea-acciones {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .tarea .tarea-acciones .contenedor-titulo {
      width: 70%; }
    .tarea .tarea-acciones .contenedor-acciones {
      width: 30%; }
      .tarea .tarea-acciones .contenedor-acciones .boton-naranja,
      .tarea .tarea-acciones .contenedor-acciones .boton-verde {
        width: 100%;
        margin: 0; }
  .tarea .tarea-info .descripcion {
    font-weight: 300;
