.titulo-tarea {
  color: #d358f7;
  font-weight: 700;
  font-size: 2rem; }

.borde-tarea {
  background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
  background-repeat: repeat;
  padding: .4rem;
  border-radius: 1.5rem;
  margin-bottom: 2rem;
  animation: gradient 180s linear infinite; }
  @media (min-width: 768px) {
    .borde-tarea {
      margin: 0; } }

@keyframes gradient {
  0% {
    background-position: 0 0; }
  50% {
    background-position: -80rem 0; }
  100% {
    background-position: 0 0; } }

.campo {
  display: flex;
  flex-direction: column; }
  .campo input[type="text"],
  .campo textarea {
    padding: 1rem .5rem;
    resize: none;
    border: 0.2rem solid #9f81f7;
    border-radius: .5rem;
    outline: none; }

.confirmar-tarea .boton-rojo,
.confirmar-tarea .boton-verde {
  margin: 0; }

.confirmar-tarea .boton-verde {
